.container {
  width: 100%;
  height: 100%;
  position: relative;
  .topContainer{
    background-color: #E6EAF0;
    .top {
      height: 120px;
      width:1180px;
      margin: 0 auto;
      position: relative;
      :global(.ant-breadcrumb){
        text-align: left;
        width:200px;
        position: absolute;
        top:34px;
        font-size: 16px;
        :global(.anticon-shopping){
          margin-right: 8px;
        }
      }
      .right {
        position: absolute;
        text-align: right;
        right:165px;
        line-height: 120px;
        .search {
          width: 251px;
          height: 36px;
          color: #888888;
          margin-left:20px;
          :global(.ant-input-suffix){
            font-size:20px;
          }
        }
      }
    }
  }
  .table {
    background-color: #FFFFFF;
    width: 1180px;
    margin: 24px auto;
    :global(.ant-table-thead) {
      background-color: #F0F2F5;
    }
    :global(.ant-table-thead tr th) {
      background-color: #F0F2F5;
    }
    :global(.formName .ant-table-column-sorter) {
      left: 70px;
    }
    :global(.businessPeople .ant-table-column-sorter) {
      left: 83px;
    }
    :global(.ant-table-column-sorter) {
      color: #101010 !important;
      left: 54px;
    }
    :global(.ant-table-tbody tr:nth-child(even)) {
      background-color: rgba(240, 242, 245, 0.5);
    }
    :global(.ant-table-thead  tr   th){
      padding: 10px 8px !important;
      min-width: 142px;
    }
    :global(.ant-table-thead  tr   th:last-child){
      min-width: 180px;
    }
    td, div {
      color: rgba(16, 16, 16, 1);
      font-family: SourceHanSansSC-regular;
      font-size: 14px;
    }
    td{
      padding: 10px 8px !important;

    }
    a {
      color: rgba(101, 151, 255, 1);
      text-decoration: none;
    }
    :global(.ant-table-placeholder){
      text-align: center!important;
    }
  }
  :global(.ant-pagination-total-text) {
    margin-top: -18px;
    flex: 1;
    margin-right:0!important;
    color: rgba(16, 16, 16, 1);
    font-size: 14px;
    text-align: left;
  }
  :global(.ant-pagination) {
    width:1180px;
    margin: 27px auto;
    display:flex;
  }
}